<template>
<div>
    <loading :active.sync="isLoading" >
     <div class="dot-loader">
         <span class="dot dot1 bg-use"></span>
     <span class="dot dot2 bg-abstract"></span>
     <span class="dot dot3 bg-warning"></span>
     <span class="dot dot4 bg-alert"></span></div>
     <i class="fas fa-fish text-primary me-2 fa-3x"></i>
     <div class="dot-loader">
         <span class="dot dot5 bg-use"></span>
     <span class="dot dot6 bg-abstract"></span>
     <span class="dot dot7 bg-warning"></span>
     <span class="dot dot8 bg-alert"></span></div>
  </loading> 
<div class="container">
<CheckoutFlow :activeEl="'checkout1'"></CheckoutFlow>
    <div class="row justify-content-center align-items-center">
    <div class="col-md-11">
        <div class="row mb-2 pb-2 border-bottom border-2 align-items-center" v-for="item in cart" :key="item.id">
            <div class="col-6 col-sm-4 col-md-3 col-lg-3">
                <img :src="item.product.imageUrl" alt="item.product.title" class="cart-img">
            </div>
            <div class="col-6 col-sm-8 col-md-9 col-lg-9">
                <div class="row align-items-center">
                    <div class="col-md-6 col-sm-6">
                        <span class="badge bg-third mb-3">{{item.product.category}}</span>
                        <p class="h5">{{item.product.title}}</p>
                        <p>數量:{{item.qty}}</p>
                    </div>
                    <div class="col-md-4 col-sm-4 col-9">
                        <p class="h5">{{item.product.price | currency}}</p>
                    </div>
                    <div class="col-md-2 col-sm-2 col-3">
                        <button @click="deleteCartItem(item.id)" class="btn py-0 text-alert ">
                            <i class="fas fa-trash-alt fa-lg"></i></button>
                    </div>
                </div>
                
            </div>
        </div>
        <div class="row">

  <div class="d-flex justify-content-end py-2" v-if="cartObjLength>0">
        <div class="me-4 h5">總計</div>
          <div class="h5">{{cartObjTotal | currency}}</div>
    </div>
    <div  v-if="finalTotal!==cartObjTotal" class="d-flex justify-content-end py-2">
        <div class="text-success me-4 h5">折扣價</div>
          <div class="text-success h5">{{finalTotal}}</div>
    </div></div>
    <div class="row">
        <div class="col-md-8 col-sm-8">
<div class="input-group input-group-sm mb-3" v-if="cart.length>0">
<input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼" aria-label="請輸入優惠碼" aria-describedby="button-addon">
<div class="input-group-append">
  <button class="btn btn-outline-third" type="button" id="button-addon" @click="useCoupon">套用優惠碼</button>
</div>
</div>
</div>
    <div class="col-md-4 col-sm-4 text-end" v-if="cart.length>0">
            <router-link class="btn btn-primary text-third w-100" to="/checkoutContact">
確認結帳<i class="fas fa-angle-double-right ms-2"></i></router-link>
    </div>

</div>
<div class="row">
    <div v-if="cart.length===0" class="text-center py-5">
   <router-link class="btn btn-primary text-third" to="/product">
沒有商品了，去逛逛<i class="fas fa-angle-double-right ms-2"></i></router-link>   
</div>
</div>
 </div>
</div>
</div>
</div>
</template>
<script>
import CheckoutFlow from '../checkoutFlow';
import {mapGetters} from 'vuex'
export default{
name:'Checkout1',
components:{
CheckoutFlow,

},
data(){
return{
 coupon_code:'',
}
},
created(){
this.$store.dispatch('getCart');
this.$store.dispatch('updateCheckoutBtn',false);
},
computed:{
...mapGetters(['cart','isLoading','cartObjLength','cartObjTotal','finalTotal','checkoutVisible']),
},
methods: {

 deleteCartItem(id){
  this.$store.dispatch('deleteCartItem',id);
},
useCoupon(){
   
 this.$store.dispatch('useCoupon', this.coupon_code);
}

},
}
</script>