<template>
    <div>
        <loading :active.sync="isLoading" >
         <div class="dot-loader">
         <span class="dot dot1 bg-use"></span>
         <span class="dot dot2 bg-abstract"></span>
         <span class="dot dot3 bg-warning"></span>
         <span class="dot dot4 bg-alert"></span></div>
         <i class="fas fa-fish text-primary me-2 fa-3x"></i>
         <div class="dot-loader">
         <span class="dot dot5 bg-use"></span>
         <span class="dot dot6 bg-abstract"></span>
         <span class="dot dot7 bg-warning"></span>
         <span class="dot dot8 bg-alert"></span></div>
        </loading> 
        <div class="container">
            <CheckoutFlow :activeEl="'checkout2'"></CheckoutFlow>
            <div class="row justify-content-center align-items-center">
        <div class="col-md-11">
             <ValidationObserver v-slot="{ invalid }">
            <form  @submit.prevent="createOrder" >
            <div class="row">
                <div class="col-md-6  mb-4">
                <ValidationProvider rules="required" v-slot="{ errors, classes }">           
                 <label for="userName">姓名</label>
                 <input type="text" name="姓名" id="userName" class="form-control " v-model="user.name" :class="classes"> 
                   <span class="invalid-feedback">{{ errors[0] }}</span>
                   </ValidationProvider> 
                </div>
                <div class="col-md-6  mb-4">
                    <ValidationProvider rules="required|email" v-slot="{ errors, classes }">
                     <label for="email">信箱</label>
                 <input type="email" name="信箱" id="email" v-model="user.email" class="form-control" :class="classes">
                   <span class="invalid-feedback" >{{ errors[0] }}</span>
                   </ValidationProvider> 
                </div>
            </div>
                <div class="row">
                <div class="col-md-6  mb-4">
                 <ValidationProvider rules="required" v-slot="{ errors, classes }">
                 <label for="phone">電話</label>
                 <input type="tel" name="電話" id="phone" v-model="user.tel" class="form-control" :class="classes">
                 <span class="invalid-feedback">{{ errors[0] }}</span>
                   </ValidationProvider> 
                 </div>
            
                <div class="col-md-6 mb-4">
                   <ValidationProvider rules="required" v-slot="{ errors, classes }">
                     <label for="email">付款方式</label>
                <select class="form-select " :class="classes" name="付款方式">
                    <option value="payafter" selected>貨到付款</option>
                    <option value="creditcard">信用卡</option>
                </select>
                 <span class="invalid-feedback">{{ errors[0] }}</span>
                  </ValidationProvider> 
                </div>
            </div>
                <div class="row">
                <div class="col-md-12  mb-4">
                <ValidationProvider rules="required" v-slot="{ errors, classes }">
                 <label for="address">地址</label>
                 <input type="text" name="地址" id="address" v-model="user.address" class="form-control" :class="classes">
                  <span class="invalid-feedback" >{{ errors[0] }}</span>
                   </ValidationProvider> 
                </div>
            </div>
            <div class="row">
                <div class="col-md-12 mb-4">
                 <label for="message">留言</label>
                 <textarea name="留言" id="message" cols="30" rows="3" v-model="user.message" class="form-control"></textarea>             
                </div>
            </div>
                <div class="text-end mb-4">
                     <button type="submit" class="btn btn-primary" :disabled="invalid" >送出訂單<i class="fas fa-angle-double-right ms-2"></i></button>
                </div>
        </form>
       </ValidationObserver>
        </div>
        </div>
        </div>
    </div>
</template>
<script>
import CheckoutFlow from '../checkoutFlow'
import {mapGetters} from 'vuex'
export default{
name:'Checkout2',
components:{
    CheckoutFlow,
    
},
data(){
    return{
        user:{
        name: "",
        email: "",
        tel: "",
        address: "",
        message:''
    },
    }
},
methods: {
createOrder(){
     const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/order`;
    const vm=this;
    this.$store.dispatch('updatedLoading',true);
    //去除括號
     let textMessage = vm.user.message.trim().replace(/[&<>"'`=\/]/g,"");
    this.$http.post(api,{data:{ user:vm.user},message:textMessage}).then((res)=>{
                if(res.data.success){
              
                    vm.$router.push(`/checkoutPay/${res.data.orderId}`);
                }else{
                    vm.$store.dispatch('updateMessage', {
                        message: res.data.message,
                        status: 'danger',
                    });
                }
                          //update CART
                 vm.$store.dispatch('getCart'); 
                this.$store.dispatch('updatedLoading',false);
            }).catch((err)=>{
                console.log(err);
            })
}
},
computed:{
    ...mapGetters(['isLoading'])
}

}
</script>