<template>
    <div>
        <loading :active.sync="isLoading" >
                <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
            </loading> 
            <div class="text-end py-3">
            <button class="btn btn-info text-white" data-toggle="modal" @click="openModal(true)">
                新增優惠卷<i class="fas fa-plus ms-2"></i></button>
        </div>
        <div class="table-responsive">
          <table class="table table-striped table-sm mt-4 text-third">
            <thead>
                <th width="130">名稱</th>
                <th width="60">折扣百分比</th>
                <th width="120">到期日</th>
                <th  width="60">是否啟用</th>
                <th   width="120">編輯</th>
            </thead>
            <tbody>
                <tr v-for="(item) in coupons" :key="item.id">
                   <td>{{item.title}}</td>
                    <td>{{item.percent}}</td>
                      <td>{{item.due_date}}</td>    
        <td ><span v-if="item.is_enabled">啟用</span><span v-else>未啟用</span></td>
        <td><button class="btn btn-outline-third btn-sm" @click="openModal(false,item)">編輯</button>
        <button class="btn btn-outline-alert btn-sm" @click="openModalDelete(item)">刪除</button>
        </td>                    
                </tr>
            </tbody>
        </table>
   
          <div v-if="coupons.length==0">
               <h3 class="text-center">請新增優惠卷</h3>
               </div>
    </div>
<pagination :pagination="pagination" @getData="getCoupons"></pagination> 


<div class="modal fade"  id="AddNewModal" tabindex="-1"  aria-hidden="true">
<div class="modal-dialog modal-dialog-scrollable modal-xl">
<div class="modal-content">
<div class="modal-header bg-primary bg-gradient text-third">
<h5 class="modal-title" >{{modalTitle}}</h5>
<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
</div>
<div class="modal-body">
<form class="row g-3">
 <div class="col-12 mb-4">
              <label for="title">名稱</label>
              <input type="text" class="form-control" id="title"
                placeholder="請輸入優惠名稱" v-model="currentCuopon.title">
            </div>
              <div class="col-6 mb-4">
                <label for="discountCode">優惠碼</label>
                <input type="text" class="form-control" id="discountCode"
                  placeholder="請輸入優惠碼" v-model="currentCuopon.code">
              </div>
              <div class="col-6 mb-4">
                <label for="expireDay">到期日</label>
                <input type="date" class="form-control" id="expireDay" v-model="currentCuopon.due_date"
                  placeholder="請輸入到期日">
              </div>
              <div class="col-6 mb-4">
              <label for="discountPercent">折扣百分比</label>
                <input type="number" class="form-control" id="discountPercent"
                  placeholder="請輸入折扣百分比" v-model="currentCuopon.percent">
              </div>         
                <div class="col-6 mt-5">
              <div class="form-check mb-4  ms-1">
                   <label class="form-check-label" for="is_enabled">
                  是否啟用
                </label>
                <input class="form-check-input" type="checkbox"
                  id="is_enabled" v-model="currentCuopon.is_enabled"
                  :true-value="1" :false-value="0">
               
              </div>
          </div>
</form>
</div>
<div class="modal-footer">
<button type="button" class="btn btn-outline-third" data-bs-dismiss="modal">取消</button>
<button type="button" class="btn btn-primary text-third"  @click="updateCoupon">確認</button>
</div>
</div>
</div>
</div>

<!---delete modal-->
<div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
  aria-labelledby="delCouponModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header bg-danger bg-gradient">
        <h5 class="modal-title text-white" id="delCouponModallLabel">
          <span>刪除優惠卷</span>
        </h5>
      <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
        是否刪除 <strong class="text-danger">{{ currentCuopon.title }}</strong> 優惠卷(刪除後將無法恢復)。
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-outline-third" data-bs-dismiss="modal">取消</button>
        <button type="button" class="btn btn-danger" @click="deleteCoupon"
          >確認刪除</button>
      </div>
    </div>
  </div>
</div> 
    </div> 
</template>
<script>
import {mapGetters} from 'vuex';
import pagination from '../paginationManage';
export default{
    name:'coupon',
    data(){
        return{
        coupons:[],
        currentCuopon:{},
        pagination:{},
        isNew:false,
        modalTitle:'',
        modalUpdate:null,
        modalDelete:null
        }
    },
    components:{
        pagination
    },
    methods:{
   getCoupons(page=1){
           const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/coupons?page=${page}`;
           const vm=this;
           this.$store.dispatch('updatedLoading',true);
           this.$http.get(api).then((res)=>{
               if(res.data.success){
                vm.coupons=res.data.coupons;
                vm.pagination=res.data.pagination;
               }else{
                     vm.$store.dispatch('updateMessage', {
                        message: res.data.message,
                        status: 'danger',
                    });
               }
             this.$store.dispatch('updatedLoading',false);
           }).catch((err)=>{
                console.log(err);
            });
       },
       openModal(isNew , item){
           if(isNew){
            this.currentCuopon={};
            this.isNew=true;
            this.modalTitle='新增優惠卷';
           }else{
            this.currentCuopon=Object.assign({},item);
            this.isNew=false;
            this.modalTitle='修改優惠卷';
           }
      
          this.modalUpdate.show();
       },
       openModalDelete(item){
        this.currentCuopon=Object.assign({},item);
        this.modalDelete.show();
       },
       updateCoupon(){
       let api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/coupon`;
        let HttpMethod='post';
        const vm =this;
        this.$store.dispatch('updatedLoading',true);
        if(!vm.isNew){
           api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/coupon/${vm.currentCuopon.id}`;
           HttpMethod='put';
       }
       this.$http[HttpMethod](api ,{data:vm.currentCuopon}).then((res)=>{
           if(res.data.success){
            console.log(res.data.message);
          
           }else{
                 vm.$store.dispatch('updateMessage', {
                        message: res.data.message,
                        status: 'danger',
                    });                    
           }
             vm.getCoupons(); 
            vm.modalUpdate.hide();
           vm.$store.dispatch('updatedLoading',false);
       }).catch((err)=>{
                console.log(err);
            });
  
       },
       deleteCoupon(){
        const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/coupon/${this.currentCuopon.id}`;
        const vm=this;
        vm.$store.dispatch('updatedLoading',true);
        this.$http.delete(api).then((res)=>{
     
            if(res.data.success){
               
            }else{

                vm.$store.dispatch('updateMessage', {
                        message: res.data.message,
                        status: 'danger',
                    });
            }
             vm.modalDelete.hide();
            vm.getCoupons();
           vm.$store.dispatch('updatedLoading',false)
        }).catch((err)=>{
                console.log(err);
            });
       }
    },
    created(){
        this.getCoupons();
        this.$store.dispatch('updateCheckoutBtn',true);
    },
    mounted(){
        this.modalDelete=new bootstrap.Modal(document.getElementById('delCouponModal'));
        this.modalUpdate=new bootstrap.Modal(document.getElementById('AddNewModal'));
    },
    computed:{
    ...mapGetters(['isLoading','checkoutVisible']),
    }

}
</script>