import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Index from '../components/pages/index.vue'
import Product from '../components/pages/product.vue'
import Login from '../components/pages/login.vue'
import Content from '../components/pages/content.vue'
import Dashboard from '../views/dashboard.vue';
import DashboardIndex from '../components/pages/dashboardIndex.vue'
import ProductManage from '../components/pages/productManage.vue'
import Checkout1 from '../components/pages/checkout1.vue'
import Checkout2 from '../components/pages/checkout2.vue'
import Checkout3 from '../components/pages/checkout3.vue'
import Coupon from '../components/pages/coupon.vue'
import Order from '../components/pages/order.vue'

Vue.use(VueRouter)

const routes = [
  {
    path:'*',
    redirect:'/login'
  },

    {
    path: '/',
   /*  name: 'Home', */
    component: Home,
    children:[
      {
        path: '/',
        name: 'Index', 
        component: Index,  
      },
      {
        path: '/product/:kind?',
        name: 'Product',
        component: Product,  
      },
      {
        path: '/login',
        name: 'Login',
        component: Login
      },
      {
        path:'/content/:contentId',
        name:'Content',
        component:Content

      },
      {
        path:'/checkoutCoupon',
        name:'Checkout1',
        component:Checkout1

      },
      {
        path:'/checkoutContact',
        name:'Checkout2',
        component:Checkout2

      },
      {
        path:'/checkoutPay/:orderId',
        name:'Checkout3',
        component:Checkout3

      }
    ]
  },
  {
    path: '/admin',
    name: 'Dashboard',
    component: Dashboard,
 
    children:[
      {
        path: 'dashboard',
        name: 'DashboardIndex',
        component: DashboardIndex,
        meta: { requiresAuth: true },
      } ,
      {
        path: 'productManage',
        name: 'ProductManage',
        component: ProductManage,
        meta: { requiresAuth: true },
      } ,
      {
        path: 'coupon',
        name: 'Coupon',
        component: Coupon,
        meta: { requiresAuth: true },
      } ,
      {
        path: 'order',
        name: 'Order',
        component: Order,
        meta: { requiresAuth: true },
      } ,
    ]
  }
]

const router = new VueRouter({
  linkExactActiveClass:'active',
  routes
})

export default router
