import axios from 'axios';
export default{
    state: {
        cart:[],
        cartObj:{
           carlen:0,
          total:0,
        },
        finalTotal:0,
    
      },
      mutations: {
        CART(state, payload){
          state.cart=payload;
        },
        CARTOBJ(state,payload){
        state.cartObj.carlen=payload.carlen;
          state.cartObj.total=payload.total;
         
        },
        FINALTOTAL(state ,payload){
          state.finalTotal=payload; 
        },
      },
      actions: {
      AddtoCart(context,{id,qty=1}){
        const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/cart`;
        context.commit('LOADINGITEM',id);
    
       const cart={
       product_id: id,qty 
       }
    
       axios.post(api,{data:cart}).then((res)=>{
       if(res.data.success){
        
         context.dispatch('getCart');
       }else{
       
        const message =res.data.message;
        const danger='danger';
        const messageGroup={message,danger};
        context.commit('MESSAGESPUSH',messageGroup);
       }
       context.commit('LOADINGITEM','');
    }).catch((err)=>{
      console.log(err);
  })
      },
      getCart(context){
        const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/cart`;
          
        context.commit('LOADING',true);
          axios.get(api).then((res)=>{
          
              if(res.data.success){
                 
                  context.commit('CART',res.data.data.carts);
                  const cartObj={
                    carlen:res.data.data.carts.length,
                    total:res.data.data.total,
                  }
                  context.commit('CARTOBJ',cartObj);
                  context.commit('FINALTOTAL',res.data.data.final_total);
              }else{
                const message =res.data.message;
                const danger='danger';
                const messageGroup={message,danger};
                context.commit('MESSAGESPUSH',messageGroup);
              }
              context.commit('LOADING',false);
          }).catch((err)=>{
            console.log(err);
        })   
      },
      deleteCartItem(context, id){
        const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/cart/${id}`;
        context.commit('LOADING',true);
        axios.delete(api).then((res=>{
          if(res.data.success){
         
            context.dispatch('getCart');
          }
          else{
            const message =res.data.message;
            const danger='danger';
            const messageGroup={message,danger};
            context.commit('MESSAGESPUSH',messageGroup);
          }
          context.commit('LOADING',false);
        }
        )).catch((err)=>{
          console.log(err);
      });
        },
        useCoupon(context ,couponCode){
        const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/coupon`;
        context.commit('LOADING',true);
        axios.post(api,{data:{code:couponCode}}).then((res=>{
       if(res.data.success){
    
        context.commit('FINALTOTAL',res.data.data.final_total);
      }
      else{
        const message =res.data.message;
        const danger='danger';
        const messageGroup={message,danger};
        context.commit('MESSAGESPUSH',messageGroup);
      }
       context.commit('LOADING',false);
    })).catch((err)=>{
      console.log(err);
  });
        }
      },
      getters: {
     
        cart(state){
        return state.cart;
        },
        cartObjLength(state){
          return state.cartObj.carlen;
        },
        cartObjTotal(state){
          return state.cartObj.total;
        },
        finalTotal(state){
          return state.finalTotal;
        },
      }
}