<template>
     <div class="alert-message">
<div class="alert fade alert-alert show"  role="alert" :class="'alert-' + item.status"
      v-for="(item, i) in messages" :key="i">
      {{ item.message }}
  <button type="button" class="btn-close" @click="removeMessage(i)" aria-label="Close"></button>

</div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';

export default {
  name: 'Alert',
  data() {
    return {
     
    };
  },
  computed:{
       ...mapGetters(['messages']),
  },
  methods: {
    updateMessage(message, status) {
        this.$store.dispatch('updateMessage', { message, status });
    },
    removeMessage(num) {
      this.$store.dispatch('removeMessage', num);
    },
    removeMessageWithTiming(timestamp) {
      this.$store.dispatch('removeMessageWithTiming', timestamp);
    },
  },

};
</script>
<style scope>
.alert-message {
  position: fixed;
  max-width: 50%;
  top: 56px;
  right: 20px;
  z-index: 1100;
}
</style>