<template>
<div>
    <loading :active.sync="isLoading" >
    <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
</loading> 
<div class="container">
    <div class="row justify-content-center align-items-center"> 
<div class="col-md-6">
<form class="py-5" @submit="signin">
    <div class="text-center py-3">
        <div class="dot-group">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-group">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
    </div>
<h1 class="h3 mb-3 fw-bold text-center text-third">管理員登入</h1>

<div class="form-floating mb-3">
    <input type="email" class="form-control" id="floatingInput"  v-model="user.username">
    <label for="floatingInput">電子郵件</label>
</div>
<div class="form-floating mb-3">
    <input type="password" class="form-control" id="floatingPassword"  v-model="user.password" autocomplete="on">
    <label for="floatingPassword">密碼</label>
</div>


<button class="w-100 btn btn-lg btn-primary text-third fw-bold" type="submit">登入</button>

</form>
</div>
</div>
</div>  

</div>
</template>
<script>
import {mapGetters} from 'vuex'

export default{
name:'login',
data(){
return{
    user:{
        username:'',
        password:''
    }
}
},

computed:{
... mapGetters(['isLoading','checkoutVisible']),

},
methods: {
signin(){
        let api = `${process.env.VUE_APP_APIHOST}/admin/signin`;
        const vm =this;
        this.$store.dispatch('updatedLoading',true);
        this.$http.post(api,vm.user).then((res)=>{
            if(res.data.success){
                const token =res.data.token;
                const expired =res.data.expired;
                document.cookie=`hexToken=${token};expires=${new Date(expired)};`;
                vm.$store.dispatch('updatedLoading',false);
                 vm.$store.dispatch('getLoginStatus',true);
                vm.$router.push('/admin/dashboard');
            }else{
                vm.$store.dispatch('updateMessage', {
                 message: res.data.message,
                status: 'danger',
                });
                 vm.$store.dispatch('getLoginStatus',false);
                vm.$store.dispatch('updatedLoading',false);        
            }
        }).catch((err)=>{
                console.log(err);
            })
}
},
created(){
     this.$store.dispatch('updateCheckoutBtn',true);
}
}
</script>