<template>
    <div>
        <loading :active.sync="isLoading" >
                <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
            </loading> 
    
        <div class="table-responsive">
          <table class="table table-striped table-sm mt-4 text-third">
            <thead>
               <th width="120">購買時間</th>
               <th  width="140">Email</th>
               <th class="140">購買內容</th>
               <th class="text-end pe-3" width="100">應付金額</th>
               <th  width="100">狀態</th>
               <th   width="100">檢視</th>
            </thead>
            <tbody>
                <tr v-for="item in Orders" :key="item.id">
                   <td>{{getCreatedDate(item)}}</td>
                    <td>{{item.user.email}}</td>
                    <td>
                    <ul class="ps-0">
                    <li v-for="product in item.products" :key="product.id" class="list-insie">
                        {{product.product.title}} 數量{{product.qty}}{{product.product.unit}}
                    </li>
                    </ul>
                    </td>
                    <td class="text-end pe-3">{{item.total | currency}}</td>
                    <td><span class="text-success" v-if="item.is_paid">已付款</span><span v-else class="text-danger">未付款</span></td>
                    <td><button class="btn btn-outline-third" @click="OpenModal(item)">檢視</button></td>
                </tr>
            </tbody>
        </table>
          <div v-if="Orders.length==0">
               <h3 class="text-center">尚無訂單</h3>
         </div>
    </div>
        <pagination :pagination="OrderPagination" @getData="getOrders"></pagination>  
                    <!-- Modal -->
<div class="modal fade" id="ViewModal" tabindex="-1" aria-labelledby="ViewModalabel" aria-hidden="true">
<div class="modal-dialog modal-xl modal-dialog-scrollable" role="document">
<div class="modal-content border-0">
    <div class="modal-header bg-primary text-third">
    <h5 class="modal-title" id="ViewModalLabel">
        <span>檢視訂單</span>
    </h5>
     <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
    </div>
    <div class="modal-body">
    <form >
        <fieldset disabled class="row">
<div class="col-md-6 mb-3">
    <label for="datetime">購買日期</label>
    <input type="text" class="form-control" id="datetime" :value="getCreatedDate(currentOrder)">
</div>
<div class="col-md-6 mb-3">
    <label for="orderNo">訂單編號</label>
    <input type="text" class="form-control" id="orderNo" :value="currentOrder.id">
</div>
<div class="col-md-6 mb-3">
    <label for="total">金額</label>
    <input type="text" class="form-control" id="total" :value="currentOrder.total">
</div>
<div class="col-md-6 mb-3">
    <label for="isPaid">狀態</label>
    <input type="text" class="form-control" id="isPaid" :value="currentOrder.is_paid?'已付款':'未付款'">
</div>
<div class="col-md-12 mb-3">
        <label>購物清單</label>
        <ul class="ps-3 listReadonly"> 
            <li v-for="product in currentOrder.products" :key="product.id">{{product.product.title}} 
                數量{{product.qty}}{{product.product.unit}}</li>
        </ul>
</div>
<div class="col-md-6 mb-3">
    <label for="name">購買者</label>
    <input type="text" class="form-control" id="name"  :value="currentOrder.user.name">
</div>
<div class="col-md-6 mb-3">
    <label for="email">email</label>
    <input type="email" class="form-control" id="email"  :value="currentOrder.user.email">
</div>
<div class="col-md-4 mb-3">
    <label for="tel">電話</label>
    <input type="tel" class="form-control" id="tel" :value="currentOrder.user.tel">
</div>
<div class="col-md-8 mb-3">
    <label for="address">地址</label>
    <input type="text" class="form-control" id="address" :value="currentOrder.user.address">
</div>
    <div class="col-md-12 mb-3">
        <label>留言</label>
    <textarea name="" id="" cols="30" rows="3" class="form-control" :value="currentOrder.user.message"></textarea>
</div>
</fieldset>
</form>
    </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-primary text-third" data-bs-dismiss="modal">關閉</button>
      </div>
    </div>
</div>
</div>
    </div>
</template>
<script>
import pagination from '../paginationManage'
import {mapGetters} from 'vuex'
export default{
    name : 'order',
    components:{
        pagination
    },
    data(){
        return{
        currentOrder:{
            user:{}
        },
        viewModal:null
        }
    },
    methods:{
        getOrders(page=1){
          this.$store.dispatch('getOrder', page);
        },
        getCreatedDate(item){
         let date = new Date(item.create_at*1000);
         return `${date.getFullYear()}/${date.getMonth()+1}/${date.getDate()} ${date.getHours()}:${date.getMinutes()}`;
        },
        OpenModal(item){
            this.currentOrder=Object.assign({},item);
            this.viewModal.show();
        }

    },
    created(){
        this.getOrders();
        this.$store.dispatch('updateCheckoutBtn',true);
    },
    mounted(){
    this.viewModal= new bootstrap.Modal(document.getElementById('ViewModal'));
    },
    computed:{
      ...mapGetters(['isLoading','Orders','OrderPagination','checkoutVisible'])
    }
}
</script>