<template>
    <div class=" d-flex align-items-center order-md-1">
         <a class="cart nav-link" @click.prevent="cartVisible=!cartVisible">
      <i class="fa-lg fas fa-shopping-cart fa-lg"></i>
      <span class="cart-num badge rounded-pill bg-alert text-white"><small>{{cartObjLength}}</small></span>
      </a>
      <transition name="slide">
    <div class="cart-content bg-light rounded  py-2 px-2" v-if="cartVisible===true">
        <form class="">
    <table class="table table-sm table-striped" v-if="cartObjLength>0">
  <thead>
    <tr>
    <td></td>
    <td>品名</td>
    <td>數量</td>
    <td>單價</td>
  </tr>
  </thead>
  <tbody>
    <tr v-for="item in cart" :key="item.id">
    <td >
      <button @click="deleteCartItem(item.id)" class="btn py-0 text-alert" v-if="checkoutVisible===true">
        <i class="fas fa-trash-alt fa-sm"></i>
        </button>
        </td>
    <td>
      {{item.product.title}}
    </td>
    <td class="text-end">{{item.qty}}</td>
    <td  class="text-end">{{item.final_total | currency}}</td>
    </tr>
  </tbody>
</table>
  <div class="fix-footer bg-light">
        <div class="d-flex justify-content-end py-2 border-top" v-if="cartObjLength>0">
        <div class="me-4">總計</div>
          <div>{{finalTotal |currency}}</div>
    </div>
      <a @click="toCheckout" class="nav-link btn btn-block btn-primary mx-3 mb-2 text-third"  
      v-if="cartObjLength&&checkoutVisible===true">
結帳去<i class="fas fa-shopping-bag ms-2"></i>
 </a>
  </div>
    <div class="px-2 py-2" v-if="cartObjLength===0" >
      <router-link to="/product">來去逛逛<i class="fas fa-angle-double-right"></i></router-link></div>
  </form>
    </div>      
      </transition>
      </div>
</template>
<script>
import {mapGetters} from 'vuex'
export default{
    name:'shoppingCart',
data(){
    return{
          cartVisible:false,    
          coupon_code:'',
    }
},
computed:{
  ...mapGetters(['cart','cartObjLength','cartObjTotal','finalTotal','checkoutVisible']),
},
created(){
     this.$store.dispatch('getCart');

},
methods: {
    deleteCartItem(id){
        this.$store.dispatch('deleteCartItem',id);
    },
    toCheckout(){
        this.cartVisible=false;
    
        this.$router.push('/checkoutCoupon');
    }
},
}
</script>