<template>
    <div>
        <div class="row ">
           <div class="col-md-6 mb-4" v-for="item in product" :key="item.id">
 <router-link :to="`/content/${item.id}`">
    <div class="card bg-light h-100" >
        <div class="card-img-wrapper">
<img :src="item.imageUrl" class="card-img card-img-size" :alt="item.title"></div>
<div class="card-body">
      <h5 class="card-title  text-third"><strong>{{ item.title }}</strong></h5> 
      <div class="d-flex justify-content-between">
      <span class="text-decoration-line-through text-third "><small>{{item.origin_price |currency }}</small></span>
      <strong  class="text-alert">NT {{item.price | currency}} </strong>
      </div>
</div>
<div class="card-footer border-0 bg-light"> 
    <button class="my-2 btn btn-block btn-primary w-100 text-third" @click.prevent="addToCart(item.id)">
        加入購物車<i class="ms-2 fas fa-cart-plus"></i>
           <i class="fas fa-spinner fa-spin ms-2" v-if="loadingItem===item.id"></i>
        </button></div>
</div></router-link>
    </div>
    </div>
     </div>
</template>
<script>
export default{
name:'productCard',
props:['product'],
methods: {
  addToCart(id,qty=1){
   this.$store.dispatch('AddtoCart', { id,qty});
  }
},
computed:{
   loadingItem(){
   return this.$store.state.status.loadingItem;
}
}
}
</script>