<template>
<div>
    <!--loader-->
<loading :active.sync="isLoading" >
    <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
</loading> 
<div class="text-end py-3">
<button class="btn btn-info text-white" data-toggle="modal" @click="openModal(true)">
    建立新產品<i class="fas fa-plus ms-2"></i></button>
</div>
<div class="table-responsive">
<table class="table table-striped table-sm mt-4 text-third">
<thead class="border-bottom-2">
    <th width="120">分類</th>
    <th>產品名稱</th>
    <th  width="60">原價</th>
    <th  width="60">售價</th>
    <th  width="120">是否啟用</th>
    <th   width="120">編輯</th>
</thead>
<tbody>
    <tr v-for="(item) in products" :key="item.id" v-cloak>
        <td>{{item.category}}</td>
        <td>{{item.title}}</td>
        <td  class="text-right">{{item.origin_price | currency}}</td>
        <td  class="text-right">{{item.price | currency}}</td>    
<td ><span v-if="item.is_enabled">啟用</span><span v-else>未啟用</span></td>
<td><button class="btn btn-outline-third btn-sm" @click="openModal(false,item)">編輯</button>
<button class="btn btn-outline-alert btn-sm" @click.prevent="openModalDelete(item)">刪除</button>
</td>                    
    </tr> 
</tbody>
</table>
</div>
<div v-if="products.length==0" v-cloak>
    <h3 class="text-center">請新增產品</h3>
    </div> 
    <!---分頁--->
<pagination :pagination="pagination" @getData="getProducts"></pagination>

<!---刪除Modal--->
<div class="modal fade"  id="delProductModal" tabindex="-1"  aria-hidden="true">
<div class="modal-dialog">
<div class="modal-content">
<div class="modal-header bg-danger bg-gradient">
<h5 class="modal-title text-white" >刪除商品</h5>
<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
</div>
<div class="modal-body">
是否刪除 <strong class="text-danger">{{ currentProduct.title }}</strong>?<br> 商品(刪除後將無法恢復)。
</div>
<div class="modal-footer">
<button type="button" class="btn btn-outline-third" data-bs-dismiss="modal">取消</button>
<button type="button" class="btn btn-danger"  @click="deleteProduct">確認刪除</button>
</div>
</div>
</div>
</div>
<!--新增修改Modal-->
<div class="modal fade"  id="AddNewModal" tabindex="-1"  aria-hidden="true">
<div class="modal-dialog modal-dialog-scrollable modal-xl">
<div class="modal-content">
<div class="modal-header bg-primary bg-gradient text-third">
<h5 class="modal-title" >{{modaltitle}}</h5>
<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
</div>
<div class="modal-body">
<form class="row g-3">
<div class="col-12">
<label for="title">標題</label>
    <input type="text" class="form-control" id="title"
    placeholder="請輸入標題" v-model="currentProduct.title">
</div>
<div class="col-md-6">
<label for="category">分類</label>
    <input type="text" class="form-control" id="category"
        placeholder="請輸入分類" v-model="currentProduct.category">
</div>
<div class="col-md-6">
<label for="price">單位</label>
    <input type="unit" class="form-control" id="unit" v-model="currentProduct.unit"
        placeholder="請輸入單位">
</div>
<div class="col-md-6">
<label for="origin_price">原價</label>
    <input type="number" class="form-control" id="origin_price"
        placeholder="請輸入原價" v-model="currentProduct.origin_price">
</div>
<div class="col-md-6">
<label for="price">售價</label>
    <input type="number" class="form-control" id="price"
        placeholder="請輸入售價" v-model="currentProduct.price">
</div>
<div class="col-12">
    <label for="image">輸入圖片網址</label>
    <input type="text" class="form-control" id="image"
    placeholder="請輸入圖片連結" v-model="currentProduct.imageUrl">
</div>
<div class="col-md-6">
<img 
    class="img-fluid" alt="" :src="currentProduct.imageUrl">
</div>
<div class="col-md-6">
<label for="customFile">或 上傳圖片
    <i class="fas fa-spinner fa-spin" v-if="status.fileuploading"></i>
    </label>
    <input type="file" id="customFile" class="form-control" @change="uploadImage"
    ref="files">
</div>
<div class="col-12">
<label for="description">產品描述</label>
    <textarea type="text" class="form-control" id="description" rows="5"
    placeholder="請輸入產品描述" v-model="currentProduct.description"></textarea>
</div>
<div class="col-12">
<label for="content">說明內容</label>
    <textarea type="text" class="form-control" id="content" rows="10"
    placeholder="請輸入產品說明內容" v-model="currentProduct.content"></textarea>
</div>


<div class="form-group">
    <div class="form-check">
    <input class="form-check-input" type="checkbox"
        id="is_enabled" v-model="currentProduct.is_enabled"
        :true-value="1" :false-value="0">
    <label class="form-check-label" for="is_enabled">
        是否啟用
    </label>
    </div>
</div>
</form>
</div>
<div class="modal-footer">
<button type="button" class="btn btn-outline-third" data-bs-dismiss="modal">取消</button>
<button type="button" class="btn btn-primary text-third"  @click="updateProduct">確認</button>
</div>
</div>
</div>
</div>

</div>
</template>
<script>
import pagination from '../paginationManage';
import {mapGetters} from 'vuex'

export default {
name:'ProductManage',
data(){
return{
products:[],
currentProduct:{},
isNew:false,
pagination:{},
status:{
fileuploading:false,
},
modaltitle:'',
modalDelete:null,
modalUpdate:null
}
},
components:{
pagination,

},
methods:{
openModal(isNew ,item){
if(isNew){
this.currentProduct={};
this.isNew=true;
this.modaltitle='新增商品';
}else{
this.currentProduct=Object.assign({},item);
this.isNew=false;
this.modaltitle='編輯商品';
}
this.modalUpdate.show();

},
openModalDelete(item){
this.currentProduct=Object.assign({},item);

this.modalDelete.show();
},
getProducts(page =1){
const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/products?page=${page}`;
const vm =this;
this.$store.dispatch('updatedLoading',true);

this.$http.get(api).then((res)=>{

if(res.data.success){

    vm.products=res.data.products;
    vm.pagination=res.data.pagination;
        
}else{     
        vm.$store.dispatch('updateMessage', {
        message: res.data.message,
        status: 'danger',
    });
}

    vm.$store.dispatch('updatedLoading',false);
}).catch((err)=>{
                console.log(err);
            })  
},
deleteProduct(){
const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/product/${this.currentProduct.id}`;
const vm=this;
this.$store.dispatch('updatedLoading',true); 
        
this.$http.delete(api).then((res)=>{

    if(res.data.success){
        
        vm.modalDelete.hide();
        vm.getProducts();
    }else{
            vm.modalDelete.hide();
            vm.getProducts();
            vm.$store.dispatch('updateMessage', {
        message: res.data.message,
        status: 'danger',
        });
    }
    vm.$store.dispatch('updatedLoading',false);
}).catch((err)=>{
                console.log(err);
            });
},
uploadImage(){
const uploadedfile =this.$refs.files.files[0];
const vm =this;

vm.status.fileuploading=true;
const formdata =new FormData();
formdata.append('file-to-upload',uploadedfile);

const api =`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/upload`;
this.$http.post(api,formdata,{headers:{'Content-Type':'multipart/form-data'}}).then((res)=>{
    if(res.data.success){
    vm.$set(vm.currentProduct, 'imageUrl',res.data.imageUrl);
    }else{
        vm.$store.dispatch('updateMessage', {
        message: res.data.message,
        status: 'danger',
        });
    }
        vm.status.fileuploading=false;
        document.getElementById('customFile').value='';
}).catch((err)=>{
                console.log(err);
            })   

},
updateProduct(){
let api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/product`;
let HttpMethod='post';
const vm =this;
this.$store.dispatch('updatedLoading',true);

if(!vm.isNew){
api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/product/${vm.currentProduct.id}`;
HttpMethod='put';
}
this.$http[HttpMethod](api ,{data:vm.currentProduct}).then((res)=>{
    if(res.data.success){
            vm.modalUpdate.hide();  
        vm.getProducts();
    }else{
        
        vm.modalUpdate.hide();  
        vm.getProducts();

        vm.$store.dispatch('updateMessage', {
        message: res.data.message,
        status: 'danger',
        });
    }
        vm.$store.dispatch('updatedLoading',true);
}).catch((err)=>{
                console.log(err);
            })
}
},
computed:{
... mapGetters(['isLoading','checkoutVisible']),

},

created(){
this.getProducts();
this.$store.dispatch('updateCheckoutBtn',true);
},
mounted(){
    this.modalDelete=new bootstrap.Modal(document.getElementById('delProductModal'));
    this.modalUpdate=new bootstrap.Modal(document.getElementById('AddNewModal'));
}
}
</script>