<template>
    <div>     
      <nav class="navbar navbar-main navbar-expand-md bg-gradient navbar-dark sticky-top bg-dark flex-md-nowrap p-0 shadow">
  <div class="container-fluid">
       <button class="navbar-toggler" type="button"  data-bs-toggle="collapse" data-bs-target="#navbarContent"  aria-expanded="false">
     <i class="fas fa-bars"></i>
    </button>
  <router-link class="navbar-brand" to="/"><i class="fas fa-fish text-primary me-2 fa-lg"></i><strong  class="text-info">快樂水族</strong></router-link>
    
<shoppingCart></shoppingCart> 

    <div class="collapse navbar-collapse"  id="navbarContent">
      <ul class="navbar-nav ms-auto mb-2 mb-md-0">
        <li class="nav-item me-3">
          <router-link class="nav-link pb-0" to="/" exact-active-class="active">首頁</router-link>  
        </li>
        <li class="nav-item me-3">
         <router-link class="nav-link  pb-0" to="/product" exact-active-class="active">商品</router-link>
        </li>
      <li class="nav-item me-3"  v-show="loginStatus===false">
     <router-link class="nav-link  pb-0" to="/login" exact-active-class="active" >登入</router-link>
         </li>
        <li class="nav-item me-3 " v-show="loginStatus===true" >  
          <a href="#"  class="nav-link  pb-0" @click.prevent="signout"  >登出</a>
       </li>
      </ul>
    </div>
     </div>
</nav>
    </div>
</template>
<script>
import shoppingCart from './shoppingCart'
import {mapGetters} from 'vuex'

export default {
    name:'navbar',
   props:["loginstatus"],
   components:{
     shoppingCart
   },
   data(){
     return{
       isOpen:false
     }
   },
  methods: {
      signout(){
      let api=`${process.env.VUE_APP_APIHOST}/logout`;
      const vm=this;
      this.$http.post(api).then((response) => {
      
      if(response.data.success){
        vm.$store.dispatch('getLoginStatus',false);
        vm.$router.push('/login');
       
      }
    }) 
    } 
  },
  computed:{
    ...mapGetters(['loginStatus']),
  }
}
</script>