<template>
    <div>
   <Navbar></Navbar>
  <Alert></Alert>
        <div class="container-fluid">
  <div class="row">
<Sidebar :open="isOpen" @closeNav="isOpen=false"></Sidebar>
<main class="col-md-9 col-lg-10 ms-sm-auto px-md-4 dashboard-main" :class="{'open' :isOpen===true,'close':isOpen===false}"> <!--  -->
<div class="collapse-btn  d-md-none " @click="isOpen=!isOpen">展開<i class="fas fa-angle-double-right"></i></div>
   <router-view></router-view>
  </main>
  </div>

</div>
    </div>
</template>
<script>
import Navbar from '../components/navbar.vue';
import Sidebar from '../components/sidebar.vue';
import Alert from '../components/alertMessage.vue'

export default{
  name:'dashboard',
  data(){
    return{
    isOpen:false,
    }
  },
    components:{
        Navbar,
        Sidebar,
        Alert
    },
    created(){
    const token = document.cookie.replace(/(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/, '$1');

    this.$http.defaults.headers.common.Authorization = `${token}`;
    }
}
</script>