<template>
    <div>
        <div class="container">
            <CheckoutFlow :activeEl="'checkout3'"></CheckoutFlow>
             <div class="row justify-content-center align-items-center">
            <form class="col-md-11"  @submit.prevent="payOrder" >
            <h3  class="text-third">訂購商品資料<i class="fas fa-shopping-bag ms-2"></i></h3>
            <div class="table-responsive">
             <table class="table table-striped">
             <thead >
              <tr>
               <th>商品</th>
               <th>數量</th>
               <th class="pe-5 text-end">金額</th>
                <th class= "pe-5 text-end" >最終價</th>        
               </tr>
             </thead>
         <tbody>
             <tr v-for="item in order.products" :key="item.id">
                 <td v-cloak class="align-middle">
                     {{item.product.title}}
                    <span class="badge bg-success ms-2" v-if="item.total!==item.final_total">
                     折扣商品
                    </span>
                 </td>
                 <td  v-cloak class="align-middle">{{item.qty}}</td>
                 <td  v-cloak class=" pe-5 text-end"  v-if="item.total!=item.final_total"><del>{{item.total | currency}}</del></td>
                 <td  v-cloak class="pe-5 text-end"  v-if="item.total!=item.final_total">{{item.final_total | currency}}</td>
                  <td  v-cloak class=" pe-5 text-end"  v-if="item.total==item.final_total">{{item.total | currency}}</td>
                 <td  v-cloak class=" pe-5 text-end"  v-if="item.total==item.final_total">{{item.final_total | currency}}</td>
            </tr>
         </tbody>
         <tfoot >
             <tr>
                 <td class="border-bottom-0"></td>
                 <td class="border-bottom-0"></td>
                 
                 <td colspan="2" class="pe-5 text-end border-bottom-0">
                     <span class="h5">總計:<span class="ms-2"  v-cloak v-if="order.total">
                         {{order.total | currency}}</span></span></td>
             </tr>
              
         </tfoot>
        </table>
        </div>
     
         <h3 class="text-third">訂購者資料<i class="fas fa-user ms-2"></i></h3>
        <div class="table-responsive">
              <table class="table table-striped bg-light">             
                <tbody >
                 <tr>
                     <th   class="ps-5">Email</th>
                     <td  v-cloak class="ps-5">{{ order.user.email }}</td>
                 </tr>
                 <tr>
                    <th  class="ps-5">姓名</th>
                    <td  v-cloak class="ps-5">{{ order.user.name }}</td>
                 </tr>
                <tr>
                    <th  class="ps-5">收件人電話</th>
                    <td  v-cloak class="ps-5">{{ order.user.tel }}</td>
                </tr>
                 <tr>
                     <th  class="ps-5">收件人地址</th>
                     <td  v-cloak class="ps-5">{{ order.user.address }}</td>
                </tr>
                <tr>
                 <th  class="ps-5">付款狀態</th>
                <td  class="ps-5">
                 <span v-if="!order.is_paid">尚未付款</span>
                    <span v-else class="text-success">付款完成</span>
                </td>
            </tr>
      </tbody>
    </table>
        </div>
        <div class="text-end mb-4"  v-if="order.is_paid === false">
            <button type="submit" class="btn btn-primary text-third" >確認付款<i class="fas fa-angle-double-right ms-2"></i></button>
         </div>
         <div class="text-center mb-4" v-if="order.is_paid === true">
          <router-link class="btn btn-primary text-third" to="/">回首頁</router-link>
        </div>
            </form>
          </div>
        </div>
        <!-- Modal -->
<div class="modal fade" id="paySuccessModal" tabindex="-1" aria-labelledby="paySuccessModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header bg-primary">
        <h5 class="modal-title text-third" id="paySuccessModalLabel">付款成功</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body text-third">
        感謝您購買本店商品，商品會盡速寄出，約3-5天到貨~
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-primary text-third" data-bs-dismiss="modal">關閉</button>
      </div>
    </div>
  </div>
</div>

    </div>
</template>
<script>
import CheckoutFlow from '../checkoutFlow'
export default{
name:'Checkout3',
components:{
    CheckoutFlow
},
data(){
    return{
        orderId:'',
        order:{
            user:{

            }
        },
        isDiscount:false
    }
},
methods:{
    getOrder(){
      const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/order/${this.orderId}`;
            const vm=this;
            this.$store.dispatch('updatedLoading',true);
            this.$http.get(api).then((res)=>{
                if(res.data.success){
                 
                    vm.order=Object.assign({},res.data.order);
                }else{
                   vm.$store.dispatch('updateMessage', {
                        message: res.data.message,
                        status: 'danger',
                    });
                  
                }
               this.$store.dispatch('updatedLoading',false);
            }).catch((err)=>{
                console.log(err);
            });
    },
     payOrder(){
           const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/pay/${this.orderId}`;
            const vm=this;
            this.$store.dispatch('updatedLoading',true);
            this.$http.post(api).then((res)=>{
                if(res.data.success){
                   var Modal = new bootstrap.Modal(document.getElementById('paySuccessModal'));
                   Modal.show();
                   vm.getOrder();
                }else{
                    vm.$store.dispatch('updateMessage', {
                        message: res.data.message,
                        status: 'danger',
                    });                  
                }
                      
                vm.$store.dispatch('updatedLoading',false);
         
            }).catch((err)=>{
                console.log(err);
            });
     }
},
created(){
   this.orderId= this.$route.params.orderId;
   this.getOrder();
}
}
</script>