<template>
<div>
    <!--loader-->
<loading :active.sync="isLoading" >
     <div class="dot-loader">
         <span class="dot dot1 bg-use"></span>
     <span class="dot dot2 bg-abstract"></span>
     <span class="dot dot3 bg-warning"></span>
     <span class="dot dot4 bg-alert"></span></div>
     <i class="fas fa-fish text-primary me-2 fa-3x"></i>
     <div class="dot-loader">
         <span class="dot dot5 bg-use"></span>
     <span class="dot dot6 bg-abstract"></span>
     <span class="dot dot7 bg-warning"></span>
     <span class="dot dot8 bg-alert"></span></div>
  </loading> 
<div class="container py-5">
    <div class="row">
<div class="col-md-6">
<div class="bg-cover content-img mb-3" :style="{ backgroundImage: 'url(' + Product.imageUrl + ')' }" v-cloak >
</div>
</div>
<div class="col-md-6">
<h1 class="h3 fw-bold" v-cloak >{{Product.title}}</h1>
<div class="d-flex py-3 align-items-center">
<span class="text-decoration-line-through text-third me-3" v-cloak>
NT ${{Product.origin_price}}</span>
<strong class="h3 fw-bold text-alert" v-cloak>NT ${{Product.price}} </strong>
</div>
<div class="row">
<div class="col-6">
<div class="input-group mb-3">
<button class="btn btn-outline-secondary" @click.prevent="minusQuantity" type="button" id="minus"><strong class="h4">−</strong></button>
<input type="number"  class="form-control" v-model.number="quantity" @change="VerifyNumber">
<button class="btn btn-outline-secondary" @click.prevent="addQuantity" type="button" id="plus"><strong class="h4">+</strong></button>
</div>
</div>
</div>
<div class="row">
    <div class="col-6">
      <button class="my-2 btn btn-block btn-primary w-100 text-third" @click.prevent="addToCart(Product.id, quantity)">
          加入購物車<i class="ms-2 fas fa-cart-plus"></i>
           <i class="fas fa-spinner fa-spin ms-2" v-if="loadingItem===Product.id"></i>
          </button>
    </div>
</div>
</div>
    </div>
    <hr class="my-3"> 
    <div class="row text-third ">
        <div class="col-md-6">
        <div class="mb-4">
             <h3 ><i class="fas fa-info-circle me-2"></i>商品注意事項</h3>
            <p class="py-2" v-cloak>{{Product.description}}</p></div>   
         </div>  
         <div class="col-md-6">
        <div class="mb-4">
            <h3 ><i class="fas fa-feather-alt me-2"></i>商品特色</h3>
            <p class="py-2" v-cloak><pre>{{Product.content}}</pre></p>
        </div>
         </div>
    </div>
    <div class="row">
        <h3 class="d-none d-md-block" v-if="RelativeProduct.length>0">看看類似商品</h3>
      <div class="col-md-4 d-none d-md-block" v-for="item in RelativeProduct" :key="item.id">
    <router-link :to="`/content/${item.id}`" target="_blank">
    <div class="card bg-light h-100" >
        <div class="card-img-wrapper">
<img :src="item.imageUrl" class="card-img card-img-size"  v-cloak :alt="item.title"></div>
<div class="card-body">
      <h5 class="card-title  text-third" v-cloak><strong>{{ item.title }}</strong></h5> 
      <div class="d-flex justify-content-between">
      <span class="text-decoration-line-through text-third " v-cloak><small>NT ${{item.origin_price | currency}}</small></span>
      <strong  class="text-alert" v-cloak>NT ${{item.price | currency}} </strong>
      </div>
</div>
<div class="card-footer border-0 bg-light"> 
    <button class="my-2 btn btn-block btn-primary w-100 text-third" @click.prevent="addToCart(item.id)">
        加入購物車<i class="ms-2 fas fa-cart-plus"></i>
         <i class="fas fa-spinner fa-spin ms-2" v-if="loadingItem===item.id"></i>
        </button>
   </div>
</div>
</router-link>
  </div>
</div>  
</div>

</div>
</template>
<script>
import CardGroup from '../cardGroup.vue';
import {mapGetters} from 'vuex'

export default{
name:'product',
data()
{
return{
Product:{},
RelativeProduct:[],
productId:'',
quantity: 1,
min: 1,
max: 20,
}

},
components:{
CardGroup,

},
computed:{

... mapGetters(['isLoading','checkoutVisible','loadingItem']),
},
methods: {
getProduct(){
const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/product/${this.productId}`;
const vm =this;
this.$store.dispatch('updatedLoading',true);
this.$http.get(api).then((res)=>{
if(res.data.success){
    
    vm.Product=Object.assign({},res.data.product);
     
}else{
    vm.$store.dispatch('updateMessage', {
            message: res.data.message,
            status: 'danger',
          });
}
}).then(()=>{
    this.getRelativeProduct();
   
}).catch((err)=>{
                console.log(err);
            });
},
addQuantity(){
if(this.quantity<20){
this.quantity++;
}


},
minusQuantity(){
if( this.quantity>1){
this.quantity--;
}   
},
VerifyNumber(){
if(this.quantity>this.max){
this.quantity=this.max;
}
if( this.quantity<this.min){
this.quantity=this.min;
} 
},
getRelativeProduct(){

const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/products/all`;

const vm =this;
//vuex loader
this.$http.get(api).then((res)=>{
if(res.data.success){
let tmpDataList= res.data.products;
vm.RelativeProduct= tmpDataList.filter((item,i)=>{
    return (item.category.indexOf(vm.Product.category)>-1)&&(item.title!==vm.Product.title)
   
});
vm.RelativeProduct= Array.from(vm.RelativeProduct.slice(0,3));
            
}else{
    vm.$store.dispatch('updateMessage', {
            message: res.data.message,
            status: 'danger',
          });
}
 vm.$store.dispatch('updatedLoading',false);
}).catch((err)=>{
                console.log(err);
            });
},
addToCart(id,qty=1){
   this.$store.dispatch('AddtoCart', { id,qty});
  },

},created(){
this.productId=this.$route.params.contentId;
this.getProduct();
this.$store.dispatch('updateCheckoutBtn',true);
},

}
</script>